<template>
  <transition
    :duration="{
      enter: `${timeouts.enter}`,
      leave: `${timeouts.leave}`,
    }"
    enter-active-class="fade-in-active"
    enter-class="fade-in-start"
    enter-to-class="fade-in-end"
    leave-active-class="fade-out-active"
    leave-class="fade-out-start"
    leave-to-class="fade-out-end"
    @after-enter="onEntered"
    @after-leave="onLeft"
    v-show="!expired"
  >
    <div class="timer" v-show="playing">
      <!-- leave-class="scoring-start" -->
      <transition
        appear
        :duration="{
          enter: `${timeouts.change}`,
          leave: `${timeouts.change}`,
        }"
        leave-active-class="scoring-active"
        leave-to-class="scoring-end"
        @after-leave="endScoreChange"
      >
        <div class="timer__wrapper" v-show="!scoring">
          <svg
            class="timer__svg"
            viewBox="0 0 100 100"
            xmlns="http://www.w3.org/2000/svg"
            xmlns:svg="http://www.w3.org/2000/svg"
          >
            <g class="timer__circle">
              <circle
                class="timer__path-elapsed"
                :class="scoreClass"
                cx="50"
                cy="50"
                r="45"
              ></circle>
              <path
                :stroke-dasharray="strokeDasharray"
                class="timer__path-remaining"
                :class="[severity, scoreClass]"
                d="M 50, 50
            m -45, 0
            a 45,45 0 1,0 90,0
            a 45,45 0 1,0 -90,0
          "
              ></path>
            </g>
          </svg>
          <span class="timer__label" :class="scoreClass">{{
            score || formatted
          }}</span>
        </div>
      </transition>
    </div>
  </transition>
</template>

<script>
import { config, getters } from "./store";

/*Refer to previous code and this article
https://medium.com/js-dojo/how-to-create-an-animated-countdown-timer-with-vue-89738903823f */

const FULL_DASH_ARRAY = 283;

const SEVERITY = {
  ALERT: "alert",
  WARN: "warn",
  SUCCESS: "success",
};

export default {
  name: "Timer",
  data() {
    return {
      elapsed: 0,
      intervalId: null,
      scoreClass: "",
      scoring: false,
    };
  },
  computed: {
    debug() {
      return config.timer.debug;
    },
    thresholds() {
      return config.timer.thresholds;
    },
    timeouts() {
      return config.timer.timeouts;
    },
    ...{
      duration: getters.duration,
      playing: getters.playing,
      score: getters.score,
    },
    expired() {
      return this.remaining <= 0;
    },
    formatted() {
      const mins = Math.floor(this.remaining / 60000);
      const secs = Math.floor((this.remaining % 60000) / 1000);
      return secs < 10 ? `${mins}:0${secs}` : `${mins}:${secs}`;
    },
    progress() {
      return (
        Math.round((this.remaining / (this.duration * 1000)) * 10000) / 100
      );
    },
    remaining() {
      return this.duration * 1000 - this.elapsed;
    },
    severity() {
      return this.progress <= this.thresholds.warn
        ? this.progress <= this.thresholds.alert
          ? SEVERITY.ALERT
          : SEVERITY.WARN
        : SEVERITY.SUCCESS;
    },
    strokeDasharray() {
      const sofar =
        this.progress / 100 - (1 / this.duration) * (1 - this.progress / 100);
      return `${(sofar * FULL_DASH_ARRAY).toFixed(0)} ${FULL_DASH_ARRAY}`;
    },
  },

  methods: {
    onEntered() {
      this.debug && console.log("timer entered...");
      this.startTimer();
    },
    onLeft() {
      this.debug && console.log("timer left...");
      //this.elapsed = 0;
    },
    startTimer() {
      this.elapsed = 0;
      this.intervalId = setInterval(
        () => (this.elapsed += this.timeouts.interval),
        this.timeouts.interval
      );
    },
    endTimer() {
      if (this.intervalId) {
        clearInterval(this.intervalId);
        this.intervalId = null;
      }
    },
    /* Handles toggling the ephemeral score change transition */
    endScoreChange() {
      this.debug && console.log("timer scoring end...");
      this.scoring = false;
      this.scoreClass = "";
    },
  },
  watch: {
    score(newVal, oldVal) {
      this.debug && console.log("timer scoring:", oldVal, "-->", newVal);
      this.scoring = true;
      this.scoreClass = newVal > oldVal ? "hit" : "miss";
    },
    expired(newVal, oldVal) {
      if (!oldVal && newVal) {
        this.endTimer();
        this.$emit("timer-expired");
      }
    },
  },
  mounted() {
    this.debug && console.log(this.$options.name, "mounted...");
  },
  unmounted() {
    this.debug && console.log("clearing timer...");
    this.endTimer();
  },
};
</script>

<style scoped lang="scss">
.fade-in-active {
  opacity: 0;
  transition: opacity 1s cubic-bezier(0.4, 0, 0.2, 1);
}

.fade-in-start {
  opacity: 0;
}

.fade-in-end {
  opacity: 1;
}

.fade-out-active {
  opacity: 1;
  transition: opacity 1s cubic-bezier(0.4, 0, 0.2, 1);
}

.fade-out-start {
  opacity: 1;
}

.fade-out-end {
  opacity: 0;
}

.scoring-active {
  transform: scale(1, 1);
  transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.scoring-end {
  transform: scale(1.05, 1.05);
}

.timer {
  position: relative;

  &__svg {
    transform: scaleX(-1);
    overflow: visible;
  }

  &__circle {
    fill: none;
    stroke: none;
  }

  &__path-elapsed {
    fill: transparent;
    stroke-width: 7px;
    stroke: #efefef;
    transition: fill 500ms ease-in-out, stroke 500m ease-in-out;
    &.hit {
      fill: rgba(0, 255, 0, 1);
      stroke: transparent;
    }
    &.miss {
      fill: rgba(255, 0, 0, 1);
      stroke: transparent;
    }
  }

  &__path-remaining {
    stroke-width: 7px;
    stroke-linecap: square;
    transform: rotate(90deg);
    transform-origin: center;
    transition: stroke 500ms ease-in-out;
    fill-rule: nonzero;
    stroke: currentColor;

    &.alert {
      color: red;
    }

    &.warn {
      color: yellow;
    }

    &.success {
      color: lime;
    }

    &.hit,
    &.miss {
      color: transparent;
      stroke: transparent;
    }
  }

  &__label {
    position: absolute;
    display: flex;
    width: 100%;
    height: 100%;
    justify-content: center;
    align-items: center;
    top: 0;
    font-size: 2em;
    font-family: monospace;
    transform: scale(1, 1);
    transition: color 50ms ease-in-out, transform 200ms linear;
    color: #111;
    font-weight: bold;
    &.hit,
    &.miss {
      color: #fff;
    }
    &.miss {
      transform: scale(1.05, 1.05);
    }
  }
}
</style>
